@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans-regular.woff2") format("woff2"), url("../fonts/ptsans-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsans-bold.woff2") format("woff2"), url("../fonts/ptsans-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

legend {
  padding-left: 0;
  padding-right: 0;
}

picture {
  width: max-content;
  height: max-content;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  line-height: 18px;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .page {
    background-color: #f2f2f2;
  }
}

.page__body {
  margin: 0;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0 0 14px 7px rgba(0, 0, 0, 0.1);
}

.page-header__logo {
  margin-bottom: -33px;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (min-width: 768px) {
  .page-header__logo {
    position: relative;
  }
}

.site-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}
@media (max-width: 767px) {
  .site-menu {
    font-size: 18px;
    font-weight: 700;
    background-color: #81b3d2;
  }
}
@media (min-width: 768px) {
  .site-menu {
    display: grid;
    grid-template-columns: 153px 153px 156px 153px 153px;
    justify-content: center;
  }
}
@media (min-width: 1200px) {
  .site-menu {
    grid-template-columns: 239px 239px 244px 239px 239px;
  }
}

.site-menu::before {
  content: "";
  grid-column: 3/4;
  grid-row: 1/2;
}

.site-menu::after {
  content: "";
  grid-column: 3/4;
  grid-row: 2/3;
}

@media (max-width: 767px) {
  .site-menu__item {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
}

@media (min-width: 768px) {
  .site-menu__item--active a {
    color: #766357;
  }
}

.site-menu__link {
  display: block;
  padding: 18px 20px;
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  font-family: "PT Sans", sans-serif;
  line-height: 1;
}
.site-menu__link:not([href]) {
  cursor: default;
}
@media (max-width: 767px) {
  .site-menu__link[href]:hover {
    background-color: #669ec0;
  }
  .site-menu__link[href]:active {
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .site-menu__link {
    padding: 22px 0 20px;
    color: #000000;
    font-size: 14px;
  }
  .site-menu__link[href]:hover {
    color: #81b3d2;
  }
  .site-menu__link[href]:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

.logo {
  text-align: center;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .logo {
    position: absolute;
    top: 0;
    left: calc(50% - 51px);
  }
}
@media (min-width: 1200px) {
