    height: 96px;
  }
}

.button {
  display: inline-block;
  font-family: "PT Sans", sans-serif;
  text-decoration: none;
  border: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #81b3d2;
  font-weight: 400;
  text-align: center;
  padding: 11px;
  font-size: 16px;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
