.main-nav {
  position: relative;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  color: #000000;
}

.main-nav__bar {
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
  grid-template-columns: 34px 1fr 34px;
  gap: 16px;
  align-items: center;
}
@media (min-width: 768px) {
  .main-nav__bar {
    margin: 0;
    display: block;
    padding: 0;
  }
}

.main-nav__toggle {
  position: relative;
  display: block;
  width: 44px;
  height: 39px;
  background-color: transparent;
  border: none;
  z-index: 4;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  display: block;
}

.main-nav__wrapper {
  z-index: 2;
  position: absolute;
  top: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    z-index: auto;
    position: static;
  }
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(-45deg);
}

.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  top: 10px;
  right: 0;
  left: 0;
  margin: 0 auto;
  content: "";
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}

.main-nav--closed .main-nav__wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__wrapper {
    display: block;
  }
}

.page-main__section {
  text-align: center;
}

.page-main__header {
