.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #eeeeee;
  counter-reset: counter-item-advantages;
  /*запускаем функцию счетчик, видимо ка только создается элемент в данном случае список */
}
@media (min-width: 768px) {
  .advantages__list {
    width: 768px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    width: 1200px;
  }
}

.advantages__item {
  text-align: center;
  counter-increment: counter-item-advantages;
  /* добавляем единицу (по умолчанию функция +1) счетчик будет увеличиватся на единицу при рендере каждого .advantages__item в принципе можно вписать это в любой элемент присутсвующий элемент например .advantages__title или .advantages__description и будет также работать и прибавляться единица */
}
@media (min-width: 768px) {
  .advantages__item {
    min-height: 256px;
  }
}

.advantages__item--large {
  padding-top: 15px;
  color: #ffffff;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .advantages__item--large {
    padding-top: 0;
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item--large:first-of-type {
  padding-top: 30px;
  background-size: 768px 36px;
  background-position: center top;
  background-repeat: no-repeat;
  background-image: url("../img/background/down-triangle.svg");
}
@media (min-width: 768px) {
  .advantages__item--large:first-of-type {
    padding-top: 0;
    background-image: none;
  }
}

.advantages__item--small:not(:last-child) .advantages__description {
  border-bottom: 1px solid #d6d6d6;
}
@media (min-width: 768px) {
  .advantages__item--small:not(:last-child) .advantages__description {
    border: none;
  }
}

.advantages__title {
  padding-top: 25px;
  width: 280px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .advantages__title {
    width: auto;
    padding: 42px 62px 0;
  }
}

.advantages__description {
  margin: 0 auto;
  padding-bottom: 25px;
  width: 280px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
@media (min-width: 768px) {
  .advantages__description {
    width: auto;
    padding: 0 40px 42px;
  }
}

.advantages__description::before {
  display: block;
  margin: 14px auto 22px;
  content: "— №" counter(counter-item-advantages) " —";
  /*выводим нашу переменую в контенте созданного псевдоэлемента, в ковычки фкнцию ставить нельзя потому что она воспримается как строчный контент, в кавычки ставим только сторочные элементы которые не изменяются */
}

@media (min-width: 768px) {
  .advantages__image-wrapper {
    grid-column: span 2;
  }
}

@media (min-width: 768px) {
  .advantages__item--large:nth-of-type(2n) .advantages__head-wrapper {
    order: 2;
  }
}

